import React from 'react'
import { useQ } from '../q.context'
import QTHead from './thead'
import AddRow from './add-row'
import DataRow from './data-row'

const QTable = (props) => {
  const { mouseEnter } = props
  const qdata = useQ()

  const { headers, selected, actives, cols, asArray } = qdata.state

  const { setHeader, onSelect, onActivate, addRow } = qdata.actions

  const onUpdate = () => {}
  const addColumns = true
  const addRows = true

  const showExcel = true
  const showHeaders = true

  const data = asArray.map((r) => r.map(({ cid }) => cid))

  const theadProps = {
    onUpdate,
    onSelect,
    onActivate,
    showExcel,
    showHeaders,
    addColumns,
    setHeader,
    lastCol: cols - 1,
    headers,
    selected,
    actives
  }
  const thead = <QTHead {...theadProps} />

  const rowsEls = React.useMemo(
    () =>
      false ||
      data.map((r, rowIndex) => (
        <DataRow
          key={'data-row-' + r + rowIndex}
          addColumns={addColumns}
          showExcel={showExcel}
          {...{ row: r, rowIndex }}
        />
      )),
    [addColumns, data, showExcel]
  )

  const extraRow = addRows ? (
    <AddRow
      showExcel={showExcel}
      addRow={addRow}
      addColumns={addColumns}
      cols={cols}
    />
  ) : null

  const tbody = (
    <tbody>
      {rowsEls}
      {extraRow}
    </tbody>
  )

  const tfoot = <tfoot />

  const table = (
    <table className='magic-table' onMouseEnter={mouseEnter}>
      {thead}
      {tbody}
      {tfoot}
    </table>
  )

  return table
}

export default QTable
