import React from 'react'
import ObjectCell from './object'

const ArrayCell = (props) => {
  const { value, maxLen = 34 } = props

  const items = []

  if (value && value.length) {
    value.forEach((v, i) => {
      const total = items.join(', ').length
      const current = Array.isArray(v) ? (
        <ArrayCell {...{ value: v, maxLen: maxLen - total }} />
      ) : typeof v === 'object' ? (
        <ObjectCell {...{ value: v, maxLen: maxLen - total }} />
      ) : (
        `${
          (v &&
            v.toString &&
            typeof v.toString === 'function' &&
            v.toString()) ||
          v
        }`
      )

      const len = current.length
      if (total + len < maxLen || !items) {
        items.push(current)
      }
    })
  }

  return `[ ${items.join(', ')}${items.length < value.length ? ', ...' : ''} ]`
}

export default ArrayCell
