import { utils } from 'djit'

export const keyCodes = {
  BACKSPACE: 8,
  TAB: 9,
  ENTER: 13,
  SHIFT: 16,
  CTRL: 17,
  ALT: 18,
  ESC: 27,
  SPACE: 32,
  LEFT: 37,
  UP: 38,
  DOWN: 40,
  RIGHT: 39,
  DELETE: 46,
  META: 91
}

export const djitToArray = (data, meta = false) => {
  const cells = Object.entries(data)
  const output = []
  let lastCol = 0
  let lastRow = 0
  cells.forEach(([key, value]) => {
    const { row, col } = utils.nameToAddress(key)
    if (!output[row]) {
      if (output.length < row + 1) {
        let setter = output.length
        while (output.length < row + 1) {
          output[setter++] = []
        }
      }
    }
    if (row > lastRow) lastRow = row
    if (col > lastCol) lastCol = col
    output[row][col] = {
      cid: key,
      ...value
    }
  })

  for (let row = 0; row < lastRow + 1; row++) {
    for (let col = 0; col < lastCol + 1; col++) {
      if (!output[row]) {
        output[row] = []
      }

      if (!output[row][col]) {
        const cid = utils.addressToName({ col, row })
        output[row][col] = { cid }
      }
    }
  }
  if (meta)
    return {
      result: output,
      rows: lastRow + 1,
      cols: lastCol + 1
    }
  return output
}
