import React from 'react'

import QTable from './q-table'
import QContextWrapper from './q.context'
import Djit from './react-djit'

import { Wrapper } from './style'

const Main = (props) => {
  return (
    <Wrapper>
      <Djit>
        <QContextWrapper data={props.data} id={props.id || 'my-sheet'}>
          <QTable />
        </QContextWrapper>
      </Djit>
    </Wrapper>
  )
}

export default Main
