import React from 'react'

const AddRow = (props) => {
  const { cols: lastCol, addColumns, showExcel, addRow } = props
  const cols = []

  for (let c = 0; c < lastCol; c++) {
    const key = 'end-row-' + c
    const col = <td key={key} />

    cols.push(col)
  }

  if (addColumns) {
    cols.push(<td key='end-row-end-col' />)
  }

  const rowAdder = showExcel ? (
    <td onClick={addRow} className='excel'>
      +
    </td>
  ) : null

  return (
    <tr className='end-row'>
      {rowAdder}
      {cols}
    </tr>
  )
}
export default AddRow
