import React from 'react'
import { useQ } from '../q.context'
import ExcelRow from './excel-row'
import TitleRow from './title-row'

const QTHead = (props) => {
  const {
    onUpdate,
    onSelect,
    onActivate,

    showExcel,
    showHeaders,
    addColumns,
    lastCol,
    selected,
    actives
  } = props
  const q = useQ()

  const { headers } = q.state
  const { addCol, setHeader } = q.actions

  const excelRowProps = {
    addColumns,
    addCol,
    lastCol,
    selected,
    actives,
    headers
  }

  const excelRow = showExcel && <ExcelRow {...excelRowProps} />

  const titleRowProps = {
    showExcel,
    addColumns,
    lastCol,
    onUpdate,
    onSelect,
    onActivate,
    setHeader,
    headers,
    selected,
    actives
  }
  const titleRow = showHeaders && <TitleRow {...titleRowProps} />

  return (
    <thead>
      {excelRow}
      {titleRow}
    </thead>
  )
}

export default QTHead
