import React from 'react'
import DataCol from './data-col'

const DataRow = (props) => {
  const { row, rowIndex, showExcel, addColumns } = props

  const cols = React.useMemo(
    () =>
      false ||
      row.map((cid, colIndex) => (
        <DataCol key={`data-row-${cid}`} cid={cid} colIndex={colIndex} />
      )),
    [row]
  )
  if (addColumns) cols.push(<td key='end-col' />)

  const th = React.useMemo(() => {
    if (showExcel) {
      return <th className='excel'>{rowIndex + 1}</th>
    }
  }, [showExcel, rowIndex])

  return (
    <tr>
      {th}
      {cols}
    </tr>
  )
}

export default DataRow
