import React from 'react'
import ArrayCell from './array'

const ObjectCell = (props) => {
  const { value, maxLen = 34 } = props

  const items = []
  const entries = (value && Object.entries(value)) || []

  if (entries && entries.length) {
    entries.forEach(([k, v], i) => {
      const total = items.join(', ').length
      const current = Array.isArray(v) ? (
        <ArrayCell {...{ value: v, maxLen: maxLen - total }} />
      ) : typeof v === 'object' ? (
        <ObjectCell {...{ value: v, maxLen: maxLen - total }} />
      ) : (
        `${
          (v &&
            v.toString &&
            typeof v.toString === 'function' &&
            v.toString()) ||
          v
        }`
      )
      const full = `${k}: ${current}`
      const len = full.length
      if (total + len < maxLen || !items.length) {
        items.push(full)
      }
    })
  }

  return `{ ${items.join(', ')}${
    items.length < entries.length ? ', ...' : ''
  } }`
}

export default ObjectCell
