import React, { useState, useRef, useEffect } from 'react'

const EditableCell = (props) => {
  const { input, value, selected, active, onActivate, onUpdate } = props

  const inputRef = useRef()
  const [localValue, setLocalValue] = useState(input)
  const [localActive, setLocalActive] = useState(false)

  const onSubmit = (e) => {
    e.preventDefault()
    e.stopPropagation()
  }

  const onChange = (e) => {
    setLocalValue(e.target.value)
  }

  const onDoubleClick = (e) => {
    if (!active) {
      onActivate(true)
    }
  }

  useEffect(() => {
    if (active) {
      inputRef.current.focus()
      inputRef.current.selectionStart = inputRef.current.selectionEnd = `${localValue}`.length
    }
  }, [active, localValue])

  useEffect(() => {
    if (!active && localActive && input !== localValue) {
      onUpdate(localValue)
    }
  }, [active, localActive, input, localValue, onUpdate])

  useEffect(() => {
    if (active !== localActive) {
      setLocalActive(active)
    }
  }, [active, localActive, localValue])

  const onKeyUp = (e) => {
    if (e.keyCode === 13) {
      setTimeout(() => onActivate(false), 60)
      onUpdate(localValue)
      inputRef.current.blur()
      e.stopPropagation()
    }
  }

  return (
    <div
      onDoubleClick={onDoubleClick}
      className={
        'ec' + (selected ? ' selected' : '') + (active ? ' active' : '')
      }
    >
      <aside>
        <form onSubmit={onSubmit}>
          <input
            ref={inputRef}
            type='text'
            onChange={onChange}
            onKeyUp={onKeyUp}
            value={localValue || ''}
          />
        </form>
      </aside>
      <output>{value}</output>
    </div>
  )
}

export default EditableCell
