import React, { useEffect, useState, useCallback } from 'react'
import { useDjit } from './react-djit'
import { djitToArray } from './utils'
import * as djit from 'djit'

export const QContext = React.createContext()
export const useQ = () => React.useContext(QContext)

const QContextWrapper = (props) => {
  const { id: sheetId, data, settings = {}, sheetRef, onChange } = props
  const {
    showExcel = true,
    addColumns = true,
    columns = [],
    context,
    getContext
  } = settings

  const [sheet, setSheet] = useState()
  const djitc = useDjit()

  const options = {
    showExcel,
    addColumns
  }

  const [localCells, setLocalCells] = React.useState({}) // djit cell values
  const [localHeaders, setLocalHeaders] = React.useState(columns) // table headers

  const [selected, setSelected] = React.useState([]) // selected cells
  const [actives, setActives] = React.useState([]) // active cells

  const [clipboard, setClipboard] = React.useState([])

  const [errors, setErrors] = useState({})

  const { rows, cols, result: asArray } = React.useMemo(
    () => djitToArray(localCells, true),
    [localCells]
  )
  const onBeforeSet = (key, entry, postUpdate) => {
    if (entry && React.isValidElement(entry.value)) {
      const setValue = (newValue) => {
        if (entry.value.props.setValue) {
          entry.value.props.setValue(newValue)
        } else
          postUpdate((v) => ({
            element: React.cloneElement(v.element, {
              value:
                typeof newValue === 'function' ? newValue(v.value) : newValue
            }),
            value: typeof newValue === 'function' ? newValue(v.value) : newValue
          }))
      }
      const setInstanceValue = (newValue) => {
        postUpdate((v) => ({
          element: React.cloneElement(v.element, {
            value: typeof newValue === 'function' ? newValue(v.value) : newValue
          }),
          value: typeof newValue === 'function' ? newValue(v.value) : newValue
        }))
      }
      return {
        postUpdate,
        setValue,
        ...entry,
        element: React.cloneElement(entry.value, {
          postUpdate,
          setValue,
          setInstanceValue
        }),
        type: 'react',
        value:
          entry.value && entry.value.props
            ? entry.value.props.value
            : entry.value
      }
    }
    if (
      entry &&
      entry.value &&
      React.isValidElement(entry.value.element) &&
      entry.type === 'react' &&
      !entry.element
    ) {
      return {
        ...entry,
        element: React.cloneElement(entry.value.element, {
          value: entry.value.value && entry.value.value.value
        }),
        value: entry.value
      }
    }
    if (
      entry &&
      entry.type === 'react' &&
      !React.isValidElement(entry.element)
    ) {
      const { type, ...rest } = entry || {}
      return rest
    }
    if (
      options &&
      options.onBeforeSet &&
      typeof options.onBeforeSet === 'function'
    ) {
      return options.onBeforeSet(key, entry, postUpdate)
    }
    return {
      // postUpdate,
      ...entry
    }
  }

  const getCell = (value, cell) => {
    if (cell && cell.type === 'react' && React.isValidElement(cell.element))
      return {
        type: 'react',
        value: {
          type: 'react-value',
          element: cell.element,
          value:
            cell.value && cell.value.type === 'react-value'
              ? cell.value.value
              : cell.value
        }
      }
    else if (cell && cell.type === 'react') {
      const { type, ...rest } = cell
      return rest
    }

    return cell
  }

  const getValue = (value, cell) => {
    if (value?.type === 'react-value') {
      return value.value
    }
    return value
  }

  const onCellUpdate = useCallback(
    (cid, cell) => {
      setLocalCells((v) => ({ ...v, [cid]: cell }))
      if (onChange && typeof onChange === 'function') onChange(cid, cell)
    },
    [sheetId]
  )
  useEffect(() => {
    const options = {
      context,
      getContext,

      // Subscribe to onChange
      onChange: onCellUpdate,
      onBeforeSet,
      getCell,
      getValue
    }
    if (!djitc.sheets[sheetId]) {
      const baseData = [['', '', '', '', ''], [''], [''], ['']]

      djitc.createSheet(
        sheetId,
        (data && data.length && data) || baseData,
        options
      )
      return () => {
        djitc.removeSheet(sheetId)
      }
    } else if (sheetId && sheetId in djitc.sheets) {
      djitc.sheets[sheetId].setOnChange(onCellUpdate)
      return () => {
        djitc.removeSheet(sheetId)
      }
    }
  }, [sheetId, onCellUpdate])

  useEffect(() => {}, [sheet])

  useEffect(() => {
    if (djitc?.sheets?.[sheetId]) {
      setSheet(djitc.sheets[sheetId])
      if (typeof sheetRef === 'function')
        sheetRef(sheetId, djitc.sheets[sheetId])

      const cells = djitc.sheets[sheetId].Data
      Object.entries(cells).map(([cid, cell]) =>
        setLocalCells((v) => ({ ...v, [cid]: cell }))
      )
    }
  }, [djitc?.sheets, sheetId, sheet])

  // handle 'active table' to avoid deleting/modifying cells in tables not interacted with
  const onTableActivate = (isActivate) => {
    if (isActivate) props.setActiveTable(props.tableId)
    else if (props.getActiveTable() === props.tableId) props.setActiveTable('')
  }

  // on cell selected (click / keyboard navigate)
  const onSelect = (cid, isSelected) => {
    setSelected((v) => [
      ...((!isSelected && selected) || []).filter((e) => cid),
      ...(isSelected ? [cid] : [])
    ])
    if (actives.length && actives[0] !== cid && isSelected) {
      setActives([])
    }
  }

  const addSelected = (cid, isSelected) => {}

  const onActivate = (cid, isActive) => {
    setActives(
      (v) =>
        false || [
          ...(actives || []).filter((e) => e !== cid),
          ...(isActive ? [cid] : [])
        ]
    )
  }

  const setInput = (cid, input) => {
    try {
      setErrors((v) => ({
        ...v,
        [cid]: null
      }))
      sheet[cid] = input
    } catch (error) {
      setErrors((v) => ({
        ...v,
        [cid]: error
      }))
    }
  }

  const addCol = React.useCallback(() => {
    const newCid = djit.utils.addressToName({ col: cols, row: 0 })
    setInput(newCid, '')
  }, [cols, sheet])

  const addRow = React.useCallback(() => {
    const newCid = djit.utils.addressToName({ col: 0, row: rows })
    setInput(newCid, '')
  }, [rows, sheet])

  const copyCell = (cid) => {
    setClipboard((v) => [cid, ...v.filter((c) => cid !== c)])
  }

  const computePasteValue = ({ sourceCid, targetCid, sourceCell }) => {
    const sAddress = djit.utils.nameToAddress(sourceCid)
    const tAddress = djit.utils.nameToAddress(targetCid)
    const colDiff = tAddress.col - sAddress.col
    const rowDiff = tAddress.row - sAddress.row
    const computed = djit.addressModifier(colDiff, rowDiff, sourceCell.input)

    return computed
  }

  const pasteCell = (target) => {
    const source = clipboard[0]
    const sourceCell = localCells[source]
    const computedInput = computePasteValue({
      sourceCid: source,
      targetCid: target,
      sourceCell
    })
    setInput(target, computedInput)
  }

  const state = {
    tableId: props.tableId,
    headers: localHeaders,

    cells: localCells,
    actives,
    selected,
    errors,
    rows,
    cols,

    asArray
  }
  const actions = {
    getActiveTable: () => props.getActiveTable(),
    isActiveTable: () => props.getActiveTable() === props.tableId,
    onTableActivate,
    setHeader: (col, newValue) => {
      setLocalHeaders((v) => [
        ...v.filter((e, i) => i < col),
        { ...(v.find((e, i) => i === col) || {}), ...newValue },
        ...v.filter((e, i) => i > col)
      ])
    },

    addCol,
    addRow,

    setActives,
    setSelected,
    onSelect,
    onActivate,
    addSelected,
    copyCell,
    pasteCell,

    setInput
  }

  if (!sheetId) {
    return (
      <div>
        Error: <code>id</code> is required
      </div>
    )
  }

  const contextValue = { actions, options, state, djitc }

  return (
    <QContext.Provider value={contextValue}>{props.children}</QContext.Provider>
  )
}

export default QContextWrapper
