import React, { useState, useCallback, useRef, useEffect } from 'react'
import djit from 'djit'

export const DjitContext = React.createContext()

const Djit = (props) => {
  const sheetId = 'sheet1'
  const Sheets = useRef()
  const [sheets, setSheets] = useState({})

  const getSheets = useCallback(() => {
    return Sheets.current || {}
  }, [Sheets])

  useEffect(() => {
    Sheets.current = sheets

    Object.entries(sheets).forEach(([i, sheet]) => {
      if (sheetId) {
      } else {
      }
    })
  }, [sheets])

  const context = { Math }

  const onChange = (key, value, Data) => {}

  const createSheet = useCallback(
    (id, data, options) => {
      const sheet = djit(data, {
        context,
        onChange,
        id,
        ...{
          ...options
        }
      })
      let newSheets
      setSheets((v) => {
        newSheets = { ...v, [id]: sheet }
        return newSheets
      })
    },
    [getSheets]
  )

  const removeSheet = useCallback((id) => {
    let newSheets
    setSheets((v) => {
      newSheets = {
        ...Object.entries(v)
          .filter(([k]) => k !== id)
          .reduce((a, [i, v]) => ({ ...a, [i]: v }), {})
      }
      return newSheets
    })

    setTimeout(() => {
      newSheets &&
        Object.entries(newSheets).length &&
        Object.entries(newSheets).forEach(([id, sheet]) => {
          sheet.sheetsUpdated()
        })
    }, 0)
  }, [])

  const contextValue = {
    sheets,
    createSheet,
    removeSheet,
    getSheets,
    Sheets
  }

  return (
    <DjitContext.Provider value={contextValue}>
      {props.children}
    </DjitContext.Provider>
  )
}

export default Djit
