import React from 'react'
import { useQ } from '../q.context'
import * as djit from 'djit'

const ExcelRow = (props) => {
  const { headers } = props
  const q = useQ()
  const { addColumns } = q.options
  const { addCol } = q.actions
  const { cols: lastCol } = q.state

  const extra = React.useMemo(
    () => (addColumns ? <td onClick={addCol}>+</td> : null),
    [addColumns, addCol]
  )

  const cols = React.useMemo(() => {
    const cols = []
    for (let c = 0; c < lastCol; ++c) {
      const th = (headers || [])[c]
      const colName = djit.utils.numberToLetters(c + 1)
      const width = th && th.width ? th.width : ''
      const tdProps = {
        key: 'excel-' + colName,
        ...(th && th.width ? { width: th.width } : {}),
        style: {
          width,
          minWidth: width,
          maxWidth: width
        }
      }
      const col = <td {...tdProps}>{colName}</td>
      cols.push(col)
    }
    return cols
  }, [lastCol, headers])

  const thead = (
    <tr className='excel-row'>
      <td className='corner' />
      {cols}
      {extra}
    </tr>
  )

  return thead
}

export default ExcelRow
