import React, { useCallback } from 'react'
import { useQ } from '../q.context'
import EditableCell from './editable-cell'
import ArrayCell from './cell-renderers/array'
import ObjectCell from './cell-renderers/object'

const DataCol = (props) => {
  const { cid, header } = props

  const q = useQ()
  const error = q.state.errors[cid]
  const selected = q.state.selected.includes(cid)
  const active = q.state.actives.includes(cid)
  const onSelect = useCallback(
    (isSelected) => {
      q.actions.onSelect(cid, isSelected)
    },
    [cid, q.actions]
  )
  const onActivate = (isActivated) => q.actions.onActivate(cid, isActivated)
  const addSelected = (isSelected) => q.actions.addSelected(cid, isSelected)
  const { postUpdate, ...cell } = q.state.cells[cid] || {}
  const value =
    typeof cell.value === 'string' ||
    typeof cell.value === 'number' ||
    Array.isArray(cell.value)
      ? cell.value
      : typeof cell.value === 'object' && Object.keys(cell.value).length
      ? cell.value
      : ''

  const input = cell.input

  const onUpdate = (newValue) => {
    q.actions.setInput(cid, newValue)
    onActivate(false)
  }

  const onClick = (e) => {
    if (e.altKey) {
      e.preventDefault()
      e.stopPropagation()
      return
    }
    if (e.shiftKey) {
      e.preventDefault()
      e.stopPropagation()
      document.getSelection().removeAllRanges()

      if (!selected) addSelected(true)
      addSelected(false)
    } else if (!selected) onSelect(true)
  }

  const cellType = cell.type
  const cellElement = cell.element

  const showLog = false
  const cellValue = React.useMemo(
    () =>
      showLog || value instanceof Promise ? (
        <span className='pending'>Pending...</span>
      ) : cellType === 'react' && cellElement ? (
        React.cloneElement(cellElement, { value: value.value })
      ) : (
        value
      ),
    [value, cellElement, cellType, showLog]
  )

  const output = React.useMemo(
    () =>
      header && header.compute && typeof header.compute === 'function'
        ? header.compute(value, cell.input, cell, postUpdate)
        : cellValue,
    [header, cellValue, cell, postUpdate, value]
  )

  const rendered = React.useMemo(
    () =>
      header && header.render && typeof header.render === 'function'
        ? ((v) => {
            if (header && header.type === 'component') {
              const hProps = {
                cid,
                output,
                value,
                input,
                postUpdate
              }
              const HeaderRender = header.render

              return <HeaderRender {...hProps} />
            }

            const render = header.render(v)
            if (render && typeof render === 'function')
              return render(cid, cell, postUpdate)
            return render
          })(output)
        : output,
    [header, output, cell, postUpdate, value, cid, input]
  )

  const result = React.useMemo(
    () =>
      Array.isArray(rendered) ? (
        <ArrayCell {...{ value: rendered }} />
      ) : cellType === 'component' ? (
        <span>&lt;{cell.component} /&gt;</span>
      ) : !React.isValidElement(rendered) && typeof rendered === 'object' ? (
        <ObjectCell {...{ value: rendered }} />
      ) : (
        rendered
      ),
    [cell.component, cellType, rendered]
  )

  const onKeyUp = (e) => {
    if (e.keyCode === 13 && active) {
      onActivate(false)
    }
  }

  const td = (
    <td className={'etd' + (error ? ' error' : '')} onClick={onClick}>
      <EditableCell
        error={error}
        onKeyUp={onKeyUp}
        onActivate={onActivate}
        active={active}
        selected={selected}
        value={result}
        input={input}
        onUpdate={onUpdate}
        header={header}
      />
    </td>
  )

  return td
}

export default DataCol
