import React from 'react'
import * as djit from 'djit'
import EditableCell from './editable-cell'

const TitleRow = (props) => {
  const {
    showExcel,
    addColumns,
    lastCol,
    headers,
    selected,
    actives,
    onActivate,
    onSelect,
    setHeader
  } = props

  const th = React.useMemo(
    () => (showExcel ? <th className='corner excel'>&nbsp;</th> : null),
    [showExcel]
  )

  const extra = React.useMemo(
    () => (addColumns ? <td className='end-col' /> : null),
    [addColumns]
  )

  const cols = []
  for (let c = 0; c < lastCol + 1; ++c) {
    const th = (headers || [])[c]
    const colName = djit.utils.numberToLetters(c + 1)
    const cid = `${colName}#Title`
    const isSelected = selected && selected.includes(cid)
    const isActive = actives && actives.includes(cid)

    const props = {
      input: (th && th.title) || '',
      value: (th && th.title) || <em>Unnamed</em>,
      selected: isSelected,
      active: isActive,
      onActivate: (e) => {
        onActivate(cid, !isActive)
      },
      onSelect: (e) => {
        onSelect(cid, !isSelected)
      },
      onUpdate: (e) => {
        setHeader(c, { title: e })
      }
    }
    const ec = <EditableCell {...props} />
    const colProps = {
      key: 'title-' + colName,
      className: 'etd',
      ...(th && th.width ? { width: th.width } : {})
    }
    const col = <th {...colProps}>{ec}</th>
    cols.push(col)
  }

  return (
    <tr className='head-row'>
      {th}
      {cols}
      {extra}
    </tr>
  )
}

export default TitleRow
